<!--  -->
<template>
  <div class="body">
    <!--  -->
    <shopCart-form ref="shopCartDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="title">
        <el-input v-model="queryForm.title" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="queryForm.name" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="userId">
        <el-select v-model="queryForm.userId" placeholder="" size="small" clearable>
          <el-option v-for="sysUser in sysUserDict" :key="sysUser.id" :value="sysUser.id" :label="sysUser.loginName"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="orderTime">
        <el-date-picker v-model="queryForm.orderTime" placeholder="" type="date" value-format="yyyy-MM-dd" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="goodsId">
        <el-select v-model="queryForm.goodsId" placeholder="" size="small" clearable>
          <el-option v-for="goods in goodsDict" :key="goods.id" :value="goods.id" :label="goods.name"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="color">
        <el-input v-model="queryForm.color" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="price">
        <el-input v-model="queryForm.price" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="amount">
        <el-input v-model="queryForm.amount" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="specs">
        <el-input v-model="queryForm.specs" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['shopCart:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['shopCart:delete']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-export type="primary" size="mini" action="/admin/shopCart/export" title="" v-hasPermi="['shopCart:export']"/>
      </el-col>
      <el-col :span="1.5">
        <el-import type="primary" size="mini" action="/admin/shopCart/import" title="" v-hasPermi="['shopCart:import']"/>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
      <el-table-column label="" prop="picture" sortable>
        <template slot-scope="scope">
          <el-image :src="formatUrl(scope.row.picture)" class="column-image"></el-image>
        </template>
      </el-table-column>
      <el-table-column label="" prop="title" sortable/>
      <el-table-column label="" prop="name" sortable/>
      <el-table-column label="" prop="userId" sortable>
        <template slot-scope="scope">
          <span v-for="sysUser in sysUserDict" v-if="sysUser.id==scope.row.userId">{{sysUser.loginName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="goodsId" sortable>
        <template slot-scope="scope">
          <span v-for="goods in goodsDict" v-if="goods.id==scope.row.goodsId">{{goods.name}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="color" sortable/>
      <el-table-column label="" prop="price" sortable/>
      <el-table-column label="" prop="amount" sortable/>
      <el-table-column label="" prop="specs" sortable/>
      <el-table-column label="" prop="remarks" show-overflow-tooltip sortable/>
      <el-table-column label="" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['shopCart:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['shopCart:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['shopCart:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import shopCartForm from './shopCartForm'
    export default {
      components: {shopCartForm},
      data() {
        return {
          loading: true,//
          selectedData: [],//
          total: 0, //
          sysUserDict: [],
          goodsDict: [],
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            id: "",
            picture: "",
            title: "",
            name: "",
            userId: "",
            orderTime: "",
            goodsId: "",
            color: "",
            price: "",
            amount: "",
            specs: "",
            remarks: ""
          }
        };
      },
      mounted() {
        //
        this.getList();
        //
        this.getSysUserDict();
        //
        this.getGoodsDict();
      },
      computed: {
      },
      methods: {
        //
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //
        getList() {
          this.$request.doGet('/admin/shopCart/getPage', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //
        getSysUserDict() {
          this.$request.doGet('/admin/sysUser/getList').then(res => {
            if (res.isOk) {
              this.sysUserDict = res.list;
            }
          });
        },
        //
        getGoodsDict() {
          this.$request.doGet('/admin/goods/getList').then(res => {
            if (res.isOk) {
              this.goodsDict = res.list;
            }
          });
        },
        //
        resetQuery() {
          this.$refs['queryForm'].resetFields();
        },
        //
        openDialog(option) {
          this.$refs.shopCartDialog.open(option);
        },
        //
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //
        handleDeleteAll() {
          const that = this;
          that.showConfirm('?', function () {
            if (that.selectedData.length > 0) {
              that.selectedData.forEach(function (item, index) {
                that.delete(item.id);
              });
              that.showSuccess("");
            } else {
              that.showWarn("");
            }
          });
        },
        //
        handleDelete(id) {
          const that = this;
          that.showConfirm('?', function () {
            that.delete(id);
            that.showSuccess("");
          });
        },
        //
        delete(id) {
          this.$request.doGet('/admin/shopCart/delete?id=' + id).then(res => {
            if (res.isOk) {
              this.getList();
            }
          });
        },
      }
    };
</script>



