<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="orders" ref="ordersForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="orders.id"/>
        </el-form-item>
        <el-form-item label="" prop="picture">
          <sui-photo type="orders_picture" v-model="orders.picture" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="orders.title" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="orders.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="orderTime">
          <el-date-picker v-model="orders.orderTime" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
        </el-form-item>
        <el-form-item label="" prop="goodsId">
          <el-select v-model="orders.goodsId" placeholder="" :disabled="dialog.disabled">
            <el-option v-for="goods in goodsDict" :key="goods.id" :value="goods.id" :label="goods.name"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="color">
          <el-input v-model="orders.color" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input v-model="orders.price" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="amount">
          <el-input v-model="orders.amount" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="specs">
          <el-input v-model="orders.specs" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="orderStatus">
          <el-select v-model="orders.orderStatus" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in orderStatusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="userId">
          <el-select v-model="orders.userId" placeholder="" :disabled="dialog.disabled">
            <el-option v-for="sysUser in sysUserDict" :key="sysUser.id" :value="sysUser.id" :label="sysUser.loginName"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="userPhone">
          <el-input v-model="orders.userPhone" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="userName">
          <el-input v-model="orders.userName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="userAddr">
          <el-input type="textarea" v-model="orders.userAddr" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input type="textarea" v-model="orders.remarks" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "ordersForm",
    data() {
      return {
        goodsDict:[],
        sysUserDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        orders: {
          id: "",
          picture: "",
          title: "",
          name: "",
          orderTime: "",
          goodsId: "",
          color: "",
          price: "",
          amount: "",
          specs: "",
          orderStatus: "",
          userId: "",
          userPhone: "",
          userName: "",
          userAddr: "",
          remarks: ""
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'change'}],
          title: [{required: true, message: '', trigger: 'blur'}],
          name: [{required: true, message: '', trigger: 'blur'}],
          orderTime: [{required: true, message: '', trigger: 'change'}],
          goodsId: [{required: true, message: '', trigger: 'change'}],
          color: [{required: true, message: '', trigger: 'blur'}],
          price: [{required: true, message: '', trigger: 'blur'}],
          amount: [{required: true, message: '', trigger: 'blur'}],
          specs: [{required: true, message: '', trigger: 'blur'}],
          orderStatus: [{required: true, message: '', trigger: 'change'}],
          userId: [{required: true, message: '', trigger: 'change'}],
          userPhone: [{required: true, message: '', trigger: 'blur'}],
          userName: [{required: true, message: '', trigger: 'blur'}],
          userAddr: [{required: true, message: '', trigger: 'blur'}],
          remarks: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      orderStatusDict() {
        return this.$store.getters.getDictArray('order_status');
      },
    },
    methods: {
      //
      getGoodsDict() {
        this.$request.doGet('/admin/goods/getList').then(res => {
          if (res.isOk) {
            this.goodsDict = res.list;
          }
        })
      },
      //
      getSysUserDict() {
        this.$request.doGet('/admin/sysUser/getList').then(res => {
          if (res.isOk) {
            this.sysUserDict = res.list;
          }
        })
      },
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getGoodsDict();//
        this.getSysUserDict();//
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getOrders(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getOrders(id) {
        this.$request.doGet("/admin/orders/get?id=" + id).then(res => {
          if (res.isOk) {
            this.orders = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.ordersForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/orders/sub', that.orders).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.ordersForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



