<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="userRegister" ref="userRegisterForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="userRegister.id"/>
        </el-form-item>
        <el-form-item label="" prop="headPicture">
          <sui-photo type="userRegister_headPicture" v-model="userRegister.headPicture" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="loginName">
          <el-input v-model="userRegister.loginName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="userRegister.password" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="realName">
          <el-input v-model="userRegister.realName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="userNo">
          <el-input v-model="userRegister.userNo" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="userType">
          <el-select v-model="userRegister.userType" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in userTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="userRegister.phone" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="idCard">
          <el-input v-model="userRegister.idCard" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="userRegister.email" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input type="textarea" v-model="userRegister.address" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="id" prop="userAuthorId">
          <el-input v-model="userRegister.userAuthorId" placeholder="id" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "userRegisterForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        userRegister: {
          headPicture: "",
          loginName: "",
          password: "",
          realName: "",
          userNo: "",
          userType: "",
          phone: "",
          idCard: "",
          email: "",
          address: "",
          userAuthorId: ""
        },
        //
        rules: {
          headPicture: [{required: true, message: '', trigger: 'change'}],
          loginName: [{required: true, message: '', trigger: 'blur'}],
          password: [{required: true, message: '', trigger: 'blur'}],
          realName: [{required: true, message: '', trigger: 'blur'}],
          userNo: [{required: true, message: '', trigger: 'blur'}],
          userType: [{required: true, message: '', trigger: 'change'}],
          phone: [{required: true, message: '', trigger: 'blur'}],
          idCard: [{required: true, message: '', trigger: 'blur'}],
          email: [{required: true, message: '', trigger: 'blur'}],
          address: [{required: true, message: '', trigger: 'blur'}],
          userAuthorId: [{required: true, message: 'id', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      userTypeDict() {
        return this.$store.getters.getDictArray('user_type');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getUserRegister(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getUserRegister(id) {
        this.$request.doGet("/admin/userRegister/get?id=" + id).then(res => {
          if (res.isOk) {
            this.userRegister = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.userRegisterForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/userRegister/sub', that.userRegister).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.userRegisterForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



