<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="goods" ref="goodsForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="goods.id"/>
        </el-form-item>
        <el-form-item label="" prop="picture">
          <sui-photo type="goods_picture" v-model="goods.picture" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="goods.title" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="goods.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="goods.type" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input v-model="goods.price" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="color">
          <el-input v-model="goods.color" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="specs">
          <el-input v-model="goods.specs" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input type="textarea" v-model="goods.remarks" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="fileList">
          <sui-file type="goods" :refId="goods.id" v-model="goods.fileList" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "goodsForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        goods: {
          id: "",
          picture: "",
          title: "",
          name: "",
          type: "",
          price: "",
          color: "",
          specs: "",
          remarks: ""
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'change'}],
          title: [{required: true, message: '', trigger: 'blur'}],
          name: [{required: true, message: '', trigger: 'blur'}],
          type: [{required: true, message: '', trigger: 'change'}],
          price: [{required: true, message: '', trigger: 'blur'}],
          color: [{required: true, message: '', trigger: 'blur'}],
          specs: [{required: true, message: '', trigger: 'blur'}],
          remarks: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      typeDict() {
        return this.$store.getters.getDictArray('goods_type');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getGoods(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getGoods(id) {
        this.$request.doGet("/admin/goods/get?id=" + id).then(res => {
          if (res.isOk) {
            this.goods = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.goodsForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/goods/sub', that.goods).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.goodsForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



