<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="files" ref="filesForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="files.id"/>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="files.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-input v-model="files.type" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="path">
          <el-input v-model="files.path" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="stars">
          <el-input v-model="files.stars" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input v-model="files.remarks" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="pid">
          <el-input v-model="files.pid" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "filesForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        files: {
          id: "",
          name: "",
          type: "",
          path: "",
          stars: "",
          remarks: "",
          pid: ""
        },
        //
        rules: {
          name: [{required: true, message: '', trigger: 'blur'}],
          type: [{required: true, message: '', trigger: 'blur'}],
          path: [{required: true, message: '', trigger: 'blur'}],
          stars: [{required: true, message: '', trigger: 'blur'}],
          remarks: [{required: true, message: '', trigger: 'blur'}],
          pid: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getFiles(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getFiles(id) {
        this.$request.doGet("/admin/files/get?id=" + id).then(res => {
          if (res.isOk) {
            this.files = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.filesForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/files/sub', that.files).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.filesForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



