<!--  -->
<template>
  <div class="body">
    <el-dialog width="500px" style="margin-top: 150px" :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form style="height: 300px;" :model="orders" ref="noticeForm" :rules="rules" label-width="100px" class="edit-form">
<!--          <div v-for="orders in ordersList">-->
<!--            <el-tag size="small">:</el-tag><el-tag size="small">{{orders.id}}</el-tag>-->
<!--            <el-tag size="small">:</el-tag><el-tag size="small">{{orders.price}}</el-tag>-->
<!--          </div>-->
          <el-input type="text" v-model="userInfo.userName" placeholder=""/>
          <el-input type="text" v-model="userInfo.phone" placeholder=""/>
          <el-input type="textarea" v-model="userInfo.address" placeholder=""/>
        <div style="font-size: medium;padding-top: 10px"><span>:</span><span style="color: red">{{totalPrice}}</span></div>
          <el-radio-group v-model="payType" style="margin-top: 10px">
            <el-radio :label="1"><el-image style="width: 50px;height: 50px;vertical-align:middle;" src="/static/images/client/pay/weixin.png"/></el-radio>
            <el-radio :label="2"><el-image style="width: 50px;height: 50px;vertical-align:middle;" src="/static/images/client/pay/alipay.png"/></el-radio>
            <el-radio :label="3"><el-image style="width: 50px;height: 50px;vertical-align:middle;" src="/static/images/client/pay/yunshan.png"/></el-radio>
          </el-radio-group>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"></el-button>
        <el-button size="small" type="danger" @click="subPay()"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "noticeForm",
    data() {
      return {
        payType: 2,
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        ordersList:[],
        //
        orders: {
          id: "",
          picture: "",
          title: "",
          name: "",
          orderTime: "",
          goodsId: "",
          color: "",
          price: "",
          amount: "",
          specs: "",
          orderStatus: "",
          remarks: ""
        },
      }
    },
    computed:{
      userInfo(){
        return this.$store.getters.userInfo;
      },
      totalPrice(){
        let totalPrice = 0;
        if(this.ordersList.length>0){
          for (let i = 0; i < this.ordersList.length; i++) {
            //
            let price = parseFloat(this.ordersList[i].price).toFixed(2);
            let amount = parseInt(this.ordersList[i].amount);
            totalPrice += price * amount;
          }
        }
        return totalPrice
      },
    },
    methods: {
      //
      open(option) {
        if(option.type=='cart'){//
          this.ordersList=option.ordersList;
        }else{//
          this.ordersList = new Array(option.orders);
        }
        this.showDialog(option.title, false);
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //
      subPay: function () {
        if (this.userInfo.userName == '') {
          this.showWarn("")
          return
        }
        if (this.userInfo.phone == '') {
          this.showWarn("")
          return
        }
        if (this.userInfo.address == '') {
          this.showWarn("")
          return
        }
        const params = {
          userPhone: this.userInfo.phone,
          userName: this.userInfo.userName,
          userAddr: this.userInfo.address,
          list: this.ordersList
        };
        this.$request.doPost('/api/orders/payOrder',params).then(res => {
          if (res.isOk) {
            this.cancel();
            this.$emit("reloadList")
            this.showSuccess("");
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.noticeForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



