<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="userAuthor" ref="userAuthorForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="userAuthor.id"/>
        </el-form-item>
        <el-form-item label="" prop="avatarUrl">
          <sui-photo type="userAuthor_avatarUrl" v-model="userAuthor.avatarUrl" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="nickName">
          <el-input v-model="userAuthor.nickName" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="gender">
          <el-select v-model="userAuthor.gender" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in genderDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="country">
          <el-input v-model="userAuthor.country" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="province">
          <el-input v-model="userAuthor.province" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="city">
          <el-input v-model="userAuthor.city" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="openid">
          <el-input v-model="userAuthor.openid" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="unionid">
          <el-input v-model="userAuthor.unionid" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "userAuthorForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        userAuthor: {
          avatarUrl: "",
          nickName: "",
          gender: "",
          country: "",
          province: "",
          city: "",
          openid: "",
          unionid: ""
        },
        //
        rules: {
          avatarUrl: [{required: true, message: '', trigger: 'change'}],
          nickName: [{required: true, message: '', trigger: 'blur'}],
          gender: [{required: true, message: '', trigger: 'change'}],
          country: [{required: true, message: '', trigger: 'blur'}],
          province: [{required: true, message: '', trigger: 'blur'}],
          city: [{required: true, message: '', trigger: 'blur'}],
          openid: [{required: true, message: '', trigger: 'blur'}],
          unionid: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      genderDict() {
        return this.$store.getters.getDictArray('sys_sex');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getUserAuthor(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getUserAuthor(id) {
        this.$request.doGet("/admin/userAuthor/get?id=" + id).then(res => {
          if (res.isOk) {
            this.userAuthor = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.userAuthorForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/userAuthor/sub', that.userAuthor).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.userAuthorForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



